<template>
	<div>
		<!-- <img :src="backImg" class="back" /> -->
		<mt-header fixed v-bind:title="giveChildData">
			<router-link to="/" slot="left">
				<mt-button icon="back">返回</mt-button>
			</router-link>
		</mt-header>
	</div>
</template>
<script>
import backIcon from './source/Icon@3x.png';
export default {
	data() {
		return {
			backImg: backIcon
		}
	},
	props:{
		giveChildData:{
			type:String
		}
	}
}
</script>
<style>
/* .back{
	position: absolute;
	left: 9px;
	top: 33px;
	width: 12px;
	height: 21px;
} */
</style>




